body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Home .header {
  font-size: 3em;
  text-transform: uppercase;
  padding: 1em 0;
}

.Home .body {
  max-width: 640px;
  width: 100%;
}

.Main {
  display: flex;
  flex-direction: column;
}

.MasonryLayout {
  margin: 0;
}

.MasonryLayout .item {
  width: 49%;
  margin: .5%;
}

.MasonryLayout .item .thumbnail {
  width: 100%;
}


.Detail {
  display: flex;
  justify-content: center;
  padding: 2em 0;
}

.Detail .body {
  max-width: 640px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.Detail .body .img-main {
  width: 65%;
  margin-bottom: 2em;
}

.Detail .body .txt {
  font-size: .8em;
}

.Detail .body .box-comments {
  margin-top: 1em;
  width: 100%;
}

.Detail .body .box-comments .Comment {
  margin-bottom: .1em;
}

.Comment {
  word-wrap: break-word;
}

.Comment .img-profile {
  margin-right: .2em;
  width: 1em;
  vertical-align: middle;
}

.Comment .link-user {
  text-decoration: none;
  color: #000;
}

.Comment .link-user .txt-username {
  font-size: .8em;
  font-weight: bold;
  margin-right: .5em;
}

.Comment .txt-comment {
  font-size: .8em;
  margin-right: .3em;
}

.Comment .txt-time {
  font-size: .6em;
  color: rgb(100, 100, 100);
}

